import React from 'react'
import { Link } from 'react-router-dom'

const NotFound = () => {
    return (
        <div className='not_found_con'>
            <h1 style={{ textTransform: 'uppercase', fontSize: '3rem' }}>404 Page Not Found</h1>            
            <Link to='/'>GO BACK</Link>
        </div>
    )
}

export default NotFound
